
/* Login Section */
.login-section {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #FFF5EA; /* Soft cream background */
    position: relative;
}

.login-box {
    background-color: #844794C6;
      ; /* Dark purple for login box background */
    padding: 40px;
    border-radius: 15px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    width: 100%;
    max-width: 400px;
    text-align: center;
    z-index: 2;
}

.login-box h2 {
    margin-bottom: 30px;
    font-size: 2rem;
    color: #FFF5EA; /* Soft cream for heading */
}

.textbox {
    position: relative;
    margin-bottom: 30px;
}

.textbox i {
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
    color: #A89EBC; /* Soft purple for icons */
}

.textbox input {
    width: 100%;
    padding: 10px 10px 10px 40px;
    font-size: 1rem;
    color: #A89EBC; /* Soft purple for input text */
    background-color: #FFF5EA; /* Soft cream background for inputs */
    border: none;
    border-radius: 5px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.btn {
    background-color: #EB9F9B; /* Soft pink for button background */
    color: #FFF5EA; /* Dark purple for button text */
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 1.2rem;
    cursor: pointer;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
    width: 100%;
    margin-top: 20px;
}

.btn:hover {
    background-color: #EBC6C4; /* Lighter pink on hover */
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

/* Forgot Password link inside the login box */
.forgot-password {
    display: block;
    margin-top: 15px;
    color: #FFF5EA; /* Soft cream for forgot password link */
    text-decoration: none;
    font-size: 0.9rem;
}

.forgot-password:hover {
    text-decoration: underline;
}

/* Adjusted Animated Man Image */
.animated-man-near {
    position: absolute;
    left: 150px; /* Adjust left to position the image away from the login box */
    bottom: 15%;
    z-index: 1;
    max-width: 400px;
    width: 100%;
    animation: float 3s ease-in-out infinite;
}

@keyframes float {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-10px);
    }
}

